.header {
  background: #0d171d;
  width: 100%;
  padding-top: 24px;
}

.header__list {
  display: flex;
  justify-content: space-between;
}

.header__btn-chracters {
  border-radius: 1000px;
  background: #a1d737;
  padding: 10px 20px;

  color: #0d171d;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 600;

  @media screen and (min-width: 1440px) {
    border-radius: 1000px;
    background: #a1d737;
    padding: 12px 24px;

    color: #0d171d;
    font-size: 14px;
    font-weight: 600;
  }
}

.header__btn-episodes {
  border: 1px solid rgba(250, 250, 250, 0.3);
  border-radius: 1000px;
  background: transparent;
  padding: 10px 20px;

  color: #fafafa;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 600;

  @media screen and (min-width: 1440px) {
    border-radius: 1000px;
    background: transparent;
    padding: 12px 24px;

    color: #fafafa;
    font-family: "Montserrat";
    font-size: 12px;
    font-weight: 600;
  }
}

.header__search {
  padding: 10px 18px;
  background: #0d171d;
  border-radius: 1000px;
  border: 1px solid rgba(250, 250, 250, 0.3);

  color: #fafafa;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 600;
  width: 100%;
  width: 124px;
  text-align: end;

  @media screen and (min-width: 1200px) {
    padding: 12px 0px 12px 50px;
    background: #0d171d;
    border-radius: 1000px;
    border: 1px solid rgba(250, 250, 250, 0.3);

    color: #fafafa;
    font-family: "Montserrat";
    font-size: 14px;
    font-weight: 600;
    width: 124px;
    text-align: center;
  }
}

.header__search::placeholder {
  color: #fafafa;
  font-family: "Montserrat";
  font-size: 14px;
  font-weight: 600;
}

.header__box {
  display: flex;
  justify-content: space-between;
}

.header__father {
  position: relative;
}

.header__svg-search {
  width: 18px;
  height: 18px;
  fill: #fafafa;
  position: absolute;
  top: 9;
  left: 20;

  @media screen and (min-width: 768px) {
  }

  @media screen and (min-width: 1200px) {
    top: 12;
    left: 22;
  }
}